import React from 'react'
import Aheader from '../../components/A-header/Aheader'
import Square from '../../components/Square/Square'
import Story from '../../components/story/Story'
import Formations from '../../components/formations/Formations'
import Map from '../../components/map/Map'
import CallUs from '../../components/callUs/CallUs'
import Parteners from '../../components/parteners/Parteners'
import Footer from '../../components/footer/Footer'
import './accueil.css'
import { useEffect } from 'react'


function Accueil() {

   useEffect(() => {
      const elements = document.querySelectorAll(".animate-on-scroll");
      const animationOptions = {
         threshold: 0.50
      };

      const animationObserver = new IntersectionObserver(function (entries, observer) {
         entries.forEach(entry => {
            if (entry.isIntersecting) {
               entry.target.classList.add("is-visible");
               observer.unobserve(entry.target);
            }
         });
      }, animationOptions);

      elements.forEach(element => {
         animationObserver.observe(element);
      });

      // Nettoyage de l'observer lorsque le composant est dmont
      return () => {
         elements.forEach(element => {
            animationObserver.unobserve(element);
         });
      };
   }, []);

   return (
      <>
         <Aheader/>
         <Square/>
         <Story/>
         <Formations/>
         <Parteners/>
         <CallUs/>
         <Map/>
         <Footer />
      </>
   )
}

export default Accueil
