import './partener.css'
import React from 'react';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import Lottie from 'lottie-react';
import Titre from '../Title/Titre';
import animation from './part.json'
import img1 from '../../images/p1.jpg';
import img2 from '../../images/p3.jpg';
import img3 from '../../images/p2.jpg';
import img4 from '../../images/p6.jpg';
import img5 from '../../images/p7.jpg';
import img6 from '../../images/p8.jpg';



function Parteners() {
   const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 3,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 2000,
      pauseOnHover: true,
      prevArrow: <button className="slick-prev"><FontAwesomeIcon icon={faChevronLeft} /></button>,
      nextArrow: <button className="slick-next"><FontAwesomeIcon icon={faChevronRight} /></button>,
      responsive: [
         {
            breakpoint: 1024,
            settings: {
               slidesToShow: 2,
            },
         },
         {
            breakpoint: 800,
            settings: {
               slidesToShow: 1,
            },
         },
      ],
   };

   return (
      <>
         <section className="parteners">
            <div id='parts' style={{width:'100%', display:'flex', flexDirection:'column' }}>
               <Lottie style={{width:"250px", height:"250px", margin:'0 auto'}} className='lottie-anim' animationData={animation} />
            </div>
            <Titre className="titre" titre='les partenaires de dgc' />
            <div style={{marginTop:"110px"}} className="gridsPartenaire">
               <Slider   {...settings}>
                  <div className="gridp">
                     <img src={img1} alt="Partenaire 1"/>
                  </div>
                  <div className="gridp animate-on-scrollx">
                     <img src={img2} alt="Partenaire 2"/>
                  </div>
                  <div  className="gridp animate-on-scroll">
                     <img src={img3} alt="Partenaire 3"/>
                  </div>
                  <div  className="gridp animate-on-scroll">
                     <img src={img4} alt="Partenaire 3"/>
                  </div>
                  <div  className="gridp animate-on-scroll">
                     <img src={img5} alt="Partenaire 3"/>
                  </div>
                  <div  className="gridp animate-on-scroll">
                     <img src={img6} alt="Partenaire 3"/>
                  </div>
               </Slider>
            </div>
         </section>
         <div>

         </div>
      </>
   );
}

export default Parteners;
