import './card.css';
import React, { useState } from 'react';

function Card({ info, image, title }) {
   const [showPopup, setShowPopup] = useState(false);

   const handleMoreInfoClick = () => {
      setShowPopup(true);
   };

   const handleClosePopup = () => {
      setShowPopup(false);
   };

   return (
      <>
         <div className="card">
            <img src={image} alt={title} />
            <h3>{title}</h3>
            <button onClick={handleMoreInfoClick}>Plus d'info</button>
            
            {showPopup && (
               <div className="popup">
                  <div className="popup-content">
                     <img src={image} alt={title} />
                     <p>{info}</p>
                     <button onClick={handleClosePopup}>Fermer</button>
                  </div>
               </div>
            )}
         </div>
      </>
   );
}

export default Card;
