import React, { useState, useEffect, useRef } from "react";
import Slider from "react-slick";
import './student.css'
import img1 from '../../images/study.jpg'
import img2 from '../../images/story3.jpg'
import img3 from '../../images/story4.jpg'
import img4 from '../../images/story5.jpg'





function Students() {
   const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 2,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 2000,
      pauseOnHover: true,
}
   return (
      <>
         <div style={{marginTop:"80px"}} className="slider-container">
            <Slider   {...settings}>
               <div className="gridp">
                  <img src={img1} alt="Partenaire 1"/>
               </div>
               <div className="gridp animate-on-scrollx">
                  <img src={img1} alt="Partenaire 2"/>
               </div>
               {/* <div  className="gridp animate-on-scroll">
                  <img src={img1} alt="Partenaire 3"/>
               </div>
               <div  className="gridp animate-on-scroll">
                  <img src={img1} alt="Partenaire 3"/>
               </div> */}
            </Slider>
         </div>
      </>
   );
}

export default Students;
