import './footer.css'
import Titre from '../Title/Titre'
import logo from '../../images/j.jpg';
import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faInstagram, faLinkedinIn, faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faEnvelopeOpenText, faPhoneVolume } from '@fortawesome/free-solid-svg-icons'
import { Link } from 'react-router-dom';

function Footer() {
   return (
      <>
         <footer>
            <div className="f-part">
               {/* <Titre titre='Nos coordonnes'/> */}
               
               <Link to= '/'>
                  <img src={logo} alt="" />
               </Link>
               <div className="f-grids">
            
                  <div className="f-grid">
                     <div className="f">
                        <h1>  
                           <FontAwesomeIcon className='ic' icon={faPhoneVolume}/>
                        </h1>
                        <p>(+242) 06 961 21 73</p>
                        <p>(+242) 05 586 50 94</p>
                     </div>
                  </div>
                  
                  <div className="f-grid">
                     <div className="f">
                        <h1>  
                           <FontAwesomeIcon className='ic' icon={faEnvelopeOpenText}/>              
                        </h1>
                        <p>
                           Hemiplaperceeinformation@gmail.com
                        </p>
                     </div>
                  </div>
                  
                  <div className="f-grid">
                     <div id='net' className="f Reseaux">
                        <div>
                           <a href="">
                              <FontAwesomeIcon className='icons' icon={faLinkedinIn}/>
                           </a>
                           <a href="">
                              <FontAwesomeIcon className='icons' icon={faFacebookF}/>
                           </a>
                           <a href="">
                              <FontAwesomeIcon className='icons' icon={faInstagram}/>
                           </a>
                           <a href="">
                              <FontAwesomeIcon className='icons' icon={faTwitter}/>
                           </a>
                        </div>
                        <p>...</p>
                     </div>
                  </div>
               </div>
            </div>
         </footer>
      </>
   )
}

export default Footer
