import React, { useState } from 'react';
import Topwave from './topwave/Topwave';
import Titre from '../Title/Titre';
import Botwave from './botwave/Botwave';
import Lottie from 'lottie-react';
import anime from './animation.json';
import Poles from './pole/Poles';
import Grid from './grid/Grid';
import img1 from '../../images/study.jpg';

// commercial
const dlogistique = "Cette orientation consiste  former de futurs managers qui seront  mme de grer toutes les oprations de la chane logistique et du transport maritime";
const dlogistique2 = "La finalit de cette orientation est de proposer aux tudiants issus de cursus  dominante assurance ou droit une double comptence( banque et assurance)";
const dlogistique3 = "Cette orientation consiste  former de futurs managers qui seront  mme de grer toutes les oprations de la chane logistique et du transport maritime";
const data = [
   { image: img1, title: 'Transport et logistique', info: dlogistique },
   { image: img1, title: 'Comptabilit & Gestion', info: dlogistique2 },
   { image: img1, title: 'Management Ressource H.', info: dlogistique2 },
   { image: img1, title: 'Qualit  logistique', info: dlogistique2 },
   { image: img1, title: 'Ngociation & digitalisation', info: dlogistique2 },
];

// technique

const text1 ="Cette orientation consiste  former des futurs sous ingenieurs en: forage, Rservoirs, production, gosciences,  qui seront  mme dassister les ingnieurs pour ensuite diriger dune manire autonome des socits opratrices, compagnies de service dans lespace conomique Africain francophone tout en respectant les normes internationales"
const data2 =[
   { image: img1, title: 'Maintenance industrielle', info: text1 },
   { image: img1, title: 'Gnie ptrolier et minier', info: text1 },
   { image: img1, title: 'Gnie ptrolier et minier', info: text1 },
   { image: img1, title: 'Gnie ptrolier et minier', info: text1 },
]



export default function Formations() {

   const [selectedItem, setSelectedItem] = useState(null);
   const handleMoreInfoClick = (item) => {
      setSelectedItem(item);
   };

   const handleClosePopup = () => {
      setSelectedItem(null);
   };

   return (
      <>
         <section className="sectionTwo">
            <Topwave />
            <div className="sonThree">
               <div id="forming" className="protector">
                  <div style={{ width: '100%', display: 'flex', flexDirection: 'column' }}>
                     <Lottie
                        style={{ width: '250px', height: '250px', margin: '0 auto' }}
                        className="lottie-animd animate-on-scroll"
                        animationData={anime}
                     />
                  </div>
                  <Titre titre="les diplomes de HEMIP" />
                  <div className="poles">
                     <div className="poletech">
                        <Poles part="Pole technique" />
                        <div className="grids">
                           <Grid data={data2} onMoreInfoClick={handleClosePopup}/>
                        </div>
                     </div>
                     
                     <div className="polecom">
                        <Poles part="Pole commercial" />
                        <div className="grids">
                           <Grid data={data} onMoreInfoClick={handleMoreInfoClick} />
                        </div>
                     </div>
                  </div>
               </div>
            </div>
            <Botwave />
         </section>

         {selectedItem && (
            <div className="popup">
               <div className="popup-content">
                  <h3>{selectedItem.title}</h3>
                  <p>{selectedItem.info}</p>
                  <button onClick={handleClosePopup}>Fermer</button>
               </div>
            </div>
         )}
      </>
   );
}
