import React from 'react'
import './call.css'
import Topwave from '../formations/topwave/Topwave'
import Titre from '../Title/Titre'
import Formulaire from '../formulaire/Formulaire'
import Lottie from 'lottie-react'
import animation from './hop.json'

function CallUs() {
   return (
      <>
         <section className="callus">
            <Topwave/>
            <div className="callContainer">
               <div id='phone' style={{width:'100%', display:'flex', flexDirection:'column' }}>
                  <Lottie style={{width:"250px", height:"250px", margin:'0 auto'}} className='lottie-anim animate-on-scroll' animationData={animation} />
               </div>
               <Titre titre='Contactez HEMIP par ici !' />
               <div className="formPart">
                  <div className="toCenter">
                     <Formulaire />
                  </div>
               </div>
            </div>
         </section>
      </>
   )
}

export default CallUs
