import React from 'react'
import './titre.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar } from '@fortawesome/free-solid-svg-icons';



function Titre({titre}) {

   return (
      <>
         <div class="headerText .animate-on-scroll">
            <div class="hContent animate-on-scroll">
               <h1>{titre}</h1>
               <div class="botm">
                  <div class="line"></div>
                  <div>
                  <span><FontAwesomeIcon icon={faStar} style={{color: '#eb4828'}} /></span>
                  <span><FontAwesomeIcon icon={faStar} style={{color: '#eb4828'}} /></span>
                  <span><FontAwesomeIcon icon={faStar} style={{color: '#eb4828'}} /></span>
                  </div>
                  <div class="line"></div>
               </div>
            </div>
         </div>
      </>
   )
}


export default Titre
