import React from 'react';
import Card from '../card/card';
import './grid.css';

const Grid = ({ data, onMoreInfoClick }) => {
   return (
      <div className="gridcard">
         {data.map((item, index) => (
            <Card
               key={index}
               image={item.image}
               title={item.title}
               info={item.info}
            />
         ))}
      </div>
   );
};

export default Grid;
