import React, { useEffect, useState } from 'react';
import logo from '../../images/j.jpg';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import './header.css';

function Aheader() {
   const [navbar, Setnavbar] = useState(false);
   const [isMobile, setIsMobile] = useState(false);

   // Fonction qui va changer le background color
   const changeBackground = () => {
      if (window.scrollY >= 480) {
         Setnavbar(true);
      } else {
         Setnavbar(false);
      }
   };

   useEffect(() => {
      window.addEventListener('scroll', changeBackground);
      return () => {
         window.removeEventListener('scroll', changeBackground);
      };
   }, []);

   const handleScroll = (event, targetId) => {
      event.preventDefault();
      const targetElement = document.getElementById(targetId);
      if (targetElement) {
         targetElement.scrollIntoView({ behavior: 'smooth' });
      }
   };

   return (
      <>
         <header id='hd'>
            <div id="accueil" className="header">
               <div id="navbar" className={navbar ? "headPage active" : "headPage"}>
                  <div className="logo">
                     <img src={logo} alt="Logo" />
                  </div>
                  <nav className={isMobile ? "nav-mobile" : "noMobile"}>
                     <div className="links">
                        <a href="#accueil" onClick={(e) => handleScroll(e, 'accueil')}>Accueil</a>
                        <a href="#about" onClick={(e) => handleScroll(e, 'about')}>A propos</a>
                        <a href="#forming" onClick={(e) => handleScroll(e, 'forming')}>Formation</a>
                        <a href="#parts" onClick={(e) => handleScroll(e, 'parts')}>Partenaires</a>
                        <a href="#phone" onClick={(e) => handleScroll(e, 'phone')}>Contactez</a>
                        <div className="call">
                           <Link to='/inscription'>Pre-inscription</Link>
                        </div>
                     </div>
                  </nav>
                  <button className="menu-button" onClick={() => setIsMobile(!isMobile)}>
                  <FontAwesomeIcon className='menu' icon={faBars} />
                  </button>
               </div>
               <div className="headBody">
                  <div className="presentation">
                     <h1 className="txt animate-on-scrollx">
                        <span>APPRENDRE</span> TOUT AU <br /> LONG  DE LA VIE
                     </h1>
                     <p className="animate-on-scroll">
                        Chez H.E.M.I.P, nous vous offrons l'opportunit d'largir  <br/> vos connaissances  et de btir un avenir solide.
                     </p>
                     <div className="animate-on-scrollx"><a href="#phone"  onClick={(e) => handleScroll(e, 'phone')}>Contactez-nous ds maintenant</a></div>
                  </div>
               </div>
            </div>
         </header>
      </>
   );
}

export default Aheader;
